import emailjs from 'emailjs-com';

const sendEmail = (form: HTMLFormElement): Promise<string | false> => 
    emailjs.sendForm('gmail', 'template_tua14sj', form, 'user_968wDcgPKo0Pn55BwZxDz')
        .then(result => 
            result.text)
        .catch(error => false);


export default sendEmail;