import styled from 'styled-components';

const Footer = styled.footer`
    width: 100%;
    height: 100px;
    text-align: center;
    position: relative;
    background: rgb(11, 25, 35);
    color: white;
    text-transform: uppercase;
    min-width: 350px;
    & > span {
        width: 90%;
        left: 5%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
`;

export default Footer;